.Resume {
    width: 1200px;
    border: 1px solid black;
    margin: 0px auto;
    padding: 60px 0 60px 0;
    line-height: 2;
    .Container {
        h1 {
            line-height: normal;
            @include font(black, normal, 400, 4rem);
        }
        h2 {
            @include font(gray, normal, 300, 2rem);
        }
        .Subject {
            h3 {
                border-bottom: 1px solid #CCCCCC;
                margin-bottom: 2rem;
                text-align: center;
                @include font(black, normal, 300, 3rem);
            }
        }
        .Wrapper {
            display: flex;
            margin-bottom: 1rem;
            h4 {
                line-height: normal;
                @include font(black, normal, 500, 2rem);
            }
            .Left {
                @include size(30%, 100%);
                padding-right: 5%;
                a {
                    text-decoration: none;
                    @include font(gray, italic, 300, 1.5rem);
                    &:hover {
                        color: black;
                    }
                }
            }
            .Right {
                @include size(70%, 100%);
                h5 {
                    @include font(gray, italic, 300, 1.5rem);
                }
                p {
                    @include font(black, normal, 300, 1.5rem);
                }
                .desc {
                    margin-bottom: 0.5rem;
                    li {
                        @include font(black, normal, 300, 1.5rem);
                    }
                    .foldable {
                        list-style: none;
                        details {
                            margin-left: -15px;
                            summary {
                                &:hover {
                                    color: #B2CCFF;
                                    cursor: pointer;
                                }
                            }
                        }
                    }
                }
                .keywords {
                    margin-bottom: 1rem;
                    span {
                        display: inline-block;
                        margin-right: 0.5rem;
                        padding: 1px 6px;
                        background-color: rgb(239, 239, 239);
                        line-height: normal;
                        @include font(#333, normal, 300, 1.5rem);
                        box-shadow: 0 1px 3px 0 gray;
                    }
                }
                .images {
                    margin-bottom: 1rem;
                    img {
                        display: inline-block;
                        @include size(12rem, 8rem);
                        margin-right: 1rem;
                        box-shadow: 0 1px 3px 0 gray;
                        transition: 0.3s;
                        &:hover {
                            cursor: pointer;
                            box-shadow: 0 2px 5px 2px gray;
                            transform: scale(1.05);
                        }
                    }
                }
            }
        }
    }
}

embed {
    position: absolute;
    @include size(100%, 100%);
}